/* Text--------------------> */
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "League Spartan", sans-serif;
  line-height: 1.5;
  /* Estas dos lineas deben cambiar con evento de js */
  background-color: hsl(222, 26%, 31%);
  color: hsl(0, 0%, 100%);
}

p {
  font-size: 32px;
}

.content {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

/* Layout */
.Calc {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 1440px) {
  .Calc {
    width: 50%;
  }
}

/* Header Position */
.Header-Calc {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.Header-Calc__Display {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 2rem;
}

.Header-Calc__Display--Theme {
  display: flex;
  flex-direction: row;
}
.Header-Calc__Display--Theme .Theme__Number {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Keyboard Position  */
.keyboard-Calc {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.keyboard-Calc .keyboard-Calc__Grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  width: 100%;
}
@media (min-width: 1440px) {
  .keyboard-Calc .keyboard-Calc__Grid {
    justify-items: left;
    padding-left: 16px;
  }
}
.keyboard-Calc .keyboard-Calc__Grid div p {
  margin: 1rem;
}
.keyboard-Calc .keyboard-Calc__flex {
  display: flex;
  gap: 2rem;
}
.keyboard-Calc p {
  color: hsl(222, 26%, 31%);
  font-weight: bold;
  padding: 10px;
  background-color: hsl(0, 0%, 100%);
  text-align: center;
  border-radius: 10px;
}
@media (min-width: 640px) {
  .keyboard-Calc p {
    padding: 10px 40px;
  }
}
.keyboard-Calc .keyboard-Calc__Blue {
  background-color: hsl(225, 21%, 49%);
  color: hsl(0, 0%, 100%);
  display: block;
}
.keyboard-Calc .keyboard-Calc__Blue--70 {
  width: 70%;
}
.keyboard-Calc .keyboard-Calc__Red {
  background-color: hsl(6, 63%, 50%);
  color: hsl(0, 0%, 100%);
  display: block;
  width: 70%;
}

.Screen-Calc {
  text-align: right;
  margin-right: 1rem;
  font-weight: bold;
}
.Screen-Calc p {
  font-size: 40px;
  padding-right: 53px;
}

/* Border-Radius */
.keyboard-Calc,
.Screen-Calc {
  width: 100%;
  background-color: hsl(223, 31%, 20%);
  padding: 0.5rem;
  border-radius: 10px;
}